"use client"

import { create } from "zustand"
import {
  bulkUploadApi,
  BulkUploadRecord,
  BulkUploadResponse,
} from "@/services/bulk-upload.service"

interface BulkUploadState {
  loading: boolean
  response: BulkUploadResponse | null
  records: BulkUploadRecord[]
  upload: (file: File) => Promise<void>
  reset: () => void
}

export const useBulkUploadStore = create<BulkUploadState>((set) => ({
  loading: false,
  response: null,
  records: [],

  upload: async (file) => {
    const token = localStorage.getItem("auth_token")
    if (!token) throw new Error("Unauthorized")

    set({ loading: true })

    const res = await bulkUploadApi(file, token)

    set({
      loading: false,
      response: res,
      records: res.records,
    })
  },

  reset: () => {
    set({ response: null, records: [] })
  },
}))
